@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
.header__container {
  display: flex;
  box-shadow: 1px 3px 10px rgba(0,0,0,.3);
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 3;
}
.header__link {
  color: #fff;
}
.header__link.open, .header__link.header__link.open:hover {
  color: #fff;
}

.header__link:hover {
  text-decoration: none;
  color: #fff;
}
.header__btn-hamburguer {
  width: 40px;
  height: 30px;
  margin-left: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
}
.header__btn-hamburguer.open {
  z-index: 2;
}
.header__btn-hamburguer.open .line {
  background: #fff;
}
.header__btn-hamburguer .line {
  width: 100%;
  height: 6px;
  border-radius: 10px;
  background: #fff;
}
.header__btn-hamburguer.open .line:first-child {
  -webkit-animation-name: firstLine;
          animation-name: firstLine;
}
.header__btn-hamburguer.open .line:last-child {
  -webkit-animation-name: lastLine;
          animation-name: lastLine;
}
.header__btn-hamburguer.open .line:nth-child(2) {
  -webkit-animation: disappear 0ms ease-in-out 250ms forwards;
          animation: disappear 0ms ease-in-out 250ms forwards;
}
.header__btn-hamburguer.btn-close .line:first-child {
  -webkit-animation-name: firstLineReverse;
          animation-name: firstLineReverse;
}
.header__btn-hamburguer.btn-close .line:last-child {
  -webkit-animation-name: lastLineReverse;
          animation-name: lastLineReverse;
}
.header__btn-hamburguer.btn-close .line:nth-child(2) {
  -webkit-animation: appear 500ms ease-in-out forwards;
          animation: appear 500ms ease-in-out forwards;
}
.header__btn-hamburguer .line:first-child,
.header__btn-hamburguer .line:last-child {
  -webkit-animation-duration: 500ms;
          animation-duration: 500ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}
.header__overlay {
  position: fixed;
  background: #6CA7F7;
  top: 0;
  left: 0;
  width: 100%;
  height: 0%;
  opacity: 0;
  transition: opacity .35s, visibility .35s, height .35s;
  overflow: hidden;
}
.header__overlay.open {
  opacity: .9;
  height: 100%;
  z-index: 2;
}
.header__overlay.open li {
  -webkit-animation: fadeInRight .5s ease forwards;
          animation: fadeInRight .5s ease forwards;
  -webkit-animation-delay: .35s;
          animation-delay: .35s;
}
.header__overlay.open li:nth-of-type(2) {
  -webkit-animation-delay: .4s;
          animation-delay: .4s;
}
.header__overlay.open li:nth-of-type(3) {
  -webkit-animation-delay: .45s;
          animation-delay: .45s;
}
.header__overlay.open li:nth-of-type(4) {
  -webkit-animation-delay: .50s;
          animation-delay: .50s;
}
.header__overlay .header__overlay-menu {
  position: relative;
  height: 70%;
  top: 50%;
  transform: translateY(-50%);
  font-size: 50px;
  font-family: 'Varela Round', serif;
  font-weight: 400;
  text-align: center;
}
.header__overlay ul {
  list-style: none;
  padding: 0;
  margin: 0 auto;
  display: inline-block;
  position: relative;
  height: 100%;
}
.header__overlay ul li {
  display: block;
  height: 25%;
  height: calc(100% / 4);
  min-height: 50px;
  position: relative;
  opacity: 0;
}
.header__overlay ul li a {
  display: block;
  position: relative;
  color: #FFF;
  text-decoration: none;
  overflow: hidden;
}
.header__overlay ul li a:hover:after, .header__overlay ul li a:focus:after, .header__overlay ul li a:active:after {
  width: 100%;
}
.header__overlay ul li a:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0%;
  transform: translateX(-50%);
  height: 3px;
  background: #FFF;
  transition: .35s;
}

@-webkit-keyframes fadeInRight {
  0% {
    opacity: 0;
    left: 20%;
  }
  100% {
    opacity: 1;
    left: 0;
  }
}

@keyframes fadeInRight {
  0% {
    opacity: 0;
    left: 20%;
  }
  100% {
    opacity: 1;
    left: 0;
  }
}
@-webkit-keyframes firstLine {
  0% {
    transform: translateY(0) rotate(0);
  }
  25% {
    transform: translateY(10px) rotate(0);
  }
  75% {
    transform: translateY(10px) rotate(0);
  }
  100% {
    transform: translateY(10px) rotate(45deg);
  }
}
@keyframes firstLine {
  0% {
    transform: translateY(0) rotate(0);
  }
  25% {
    transform: translateY(10px) rotate(0);
  }
  75% {
    transform: translateY(10px) rotate(0);
  }
  100% {
    transform: translateY(10px) rotate(45deg);
  }
}

@-webkit-keyframes firstLineReverse {
  0% {
    transform: translateY(10px) rotate(45deg);
  }
  25% {
    transform: translateY(10px) rotate(0);
  }
  75% {
    transform: translateY(10px) rotate(0);
  }
  100% {
    transform: translateY(0) rotate(0);
  }
}

@keyframes firstLineReverse {
  0% {
    transform: translateY(10px) rotate(45deg);
  }
  25% {
    transform: translateY(10px) rotate(0);
  }
  75% {
    transform: translateY(10px) rotate(0);
  }
  100% {
    transform: translateY(0) rotate(0);
  }
}

@-webkit-keyframes lastLine {
  0% {
    transform: translateY(0) rotate(0);
  }
  25% {
    transform: translateY(-14px) rotate(0);
  }
  75% {
    transform: translateY(-14px) rotate(0);
  }
  100% {
    transform: translateY(-14px) rotate(-45deg);
  }
}

@keyframes lastLine {
  0% {
    transform: translateY(0) rotate(0);
  }
  25% {
    transform: translateY(-14px) rotate(0);
  }
  75% {
    transform: translateY(-14px) rotate(0);
  }
  100% {
    transform: translateY(-14px) rotate(-45deg);
  }
}

@-webkit-keyframes lastLineReverse {
  0% {
    transform: translateY(-14px) rotate(-45deg);
  }
  25% {
    transform: translateY(-14px) rotate(0);
  }
  75% {
    transform: translateY(-14px) rotate(0);
  }
  100% {
    transform: translateY(0) rotate(0);
  }
}

@keyframes lastLineReverse {
  0% {
    transform: translateY(-14px) rotate(-45deg);
  }
  25% {
    transform: translateY(-14px) rotate(0);
  }
  75% {
    transform: translateY(-14px) rotate(0);
  }
  100% {
    transform: translateY(0) rotate(0);
  }
}

@-webkit-keyframes disappear {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes disappear {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@-webkit-keyframes appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes appear {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
* {
  font-family: 'Montserrat', sans-serif;
}
.home__container {
  height: 100vh;
  background: url(/portfolio/static/media/home_bg.41b1d10b.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}

.home__row {
  height: 100%;
  align-items: flex-end;
  color: #fff;
  justify-content: space-between;
}

.home__main-text {
  width: 50%;
  background-color: rgba(0, 0, 0, .2);
  display: block;
  margin: 0 auto;
}

.home__main-text h1 {
  font-size: 3.5rem;
  font-family: 'Montserrat', sans-serif;
}

.home__main-text h1 span{
  font-size: 2rem;
  font-style: italic;
}

.home__social-media {
  display: grid;
  grid-template-columns: auto auto;
  align-content: center;
  align-items: center;
  justify-content: end;
}

@media only screen and (max-width: 767px) {
  .home__main-text {
    width: 100%;
  }
  .home__main-text h1 {
    font-size: 2rem;
  }
  .home__main-text h1 span{
    font-size: 1rem;
  }
  .home__text-wb {
    font-size: 1.5rem;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1023px) {
  .home__main-text {
    width: 80%;
  }
}
.about__container {
  background-color: #f5f5f5;
  height: 100vh;
  width: 100vw;
}

.about__card-container {
  border-radius: 15px;
  border: 1px solid #d1d1d1;
}

.about__card-above {
  padding: 20px;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  background: linear-gradient(90deg, rgba(49,150,243,1) 26%, rgba(58,158,243,1) 74%);
}

.about__card__img-container {
  overflow: hidden;
  position: relative;
  top: 40px;
  left: 70px;
  width: 280px;
  border: 2px solid #fff;
  border-image: none;
  border-image: initial;
  height: 280px;
  margin: 0 auto;
}

.about__card__img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.about__card-below {
  padding: 30px;
  margin-top: 10px;
}

@media only screen and (max-width: 767px) {
  .about__container {
    height: auto;
  }
  .about__card-container {
    margin-top: 80px;
    margin-bottom: 30px;
  }
  .about__card__img-container {
    top: 0px;
    left: 0px;
    width: 200px;
    height: 200px;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1023px) {
  .about__card__img-container {
    top: 0px;
    left: 0px;
    width: 200px;
    height: 200px;
  }
}
.curriculum__container {
  background-color: #f5f5f5;
  width: 100vw;
}

.curriculum__card-container {
  border-radius: 15px;
  border: 1px solid #d1d1d1d1;
  margin: 7rem 0;
}

.curriculum__card-above {
  padding: 40px;
  border-top-right-radius: 15px;
  border-top-left-radius: 15px;
  background: linear-gradient(90deg, rgba(49,150,243,1) 26%, rgba(58,158,243,1) 74%);
}

.curriculum__card-below {
  padding: 40px;
}
